@mixin clearfix {
    &::after {
        content: "";
        display: table;
        clear: both;
    }
}

@mixin check() {
    border: 1px dashed orangered;
}

@mixin init() {
    margin: inherit;
    padding: inherit;
}

// BEM (Block Element Modifier)
// Usage:   @include element('element-one') {} 
//          @include modifier('outline') {}

@mixin element($e) {
    &__#{$e} {
        @content;
    }
}

@mixin modifier($m) {
    &--#{$m} {
        @content;
    }
}